import { Injectable, inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { firstValueFrom, BehaviorSubject } from 'rxjs';

// Interface pour la structure du fichier config.json
export interface WordingConfig {
  version: string;
  active_languages: string[];
  default_lang: string;
}

// Interface r√©cursive pour les donn√©es de traduction (JSON arbitraire)
export interface TranslationMap {
  [key: string]: string | TranslationMap;
}

@Injectable({
  providedIn: 'root'
})
export class WordingService {
  private http = inject(HttpClient);

  // On utilise TranslationMap au lieu de any
  public translations$ = new BehaviorSubject<TranslationMap>({});
  public currentLang$ = new BehaviorSubject<string>('en');

  // Cache en m√©moire (RAM) uniquement - Plus s√©curis√© que localStorage
  private memoryCache = new Map<string, TranslationMap>();

  async initWording() {
    // Adapter l'URL selon o√π tu poses tes fichiers (ex: assets/i18n)
    const baseUrl = '/assets/i18n';

    try {
      console.log('üîÑ Init Wording (v15)...');
      // Typage de la r√©ponse HTTP
      const config = await firstValueFrom(this.http.get<WordingConfig>(`${baseUrl}/config.json`));

      // On v√©rifie si on a d√©j√† charger cette version
      const currentLang = this.currentLang$.value;
      const cacheKey = `wording_data_${currentLang}_v${config.version}`;

      // V√©rification du Cache M√©moire
      if (this.memoryCache.has(cacheKey)) {
        console.log('‚ö° Memory Cache Hit');
        this.translations$.next(this.memoryCache.get(cacheKey)!);
        return;
      }

      console.log('‚¨áÔ∏è T√©l√©chargement request config from:', `${baseUrl}/config.json`);
      await this.loadTranslationFromServer(config.version, baseUrl);

    } catch (error) {
      console.error("Erreur Wording:", error);
    }
  }

  private async loadTranslationFromServer(version: string, baseUrl: string) {
    const lang = this.currentLang$.value;
    const cacheKey = `wording_data_${lang}_v${version}`;
    const url = `${baseUrl}/${lang}.v${version}.json`;
    console.log('Trying to load translation from:', url);

    try {
      // Typage de la r√©ponse HTTP
      const data = await firstValueFrom(this.http.get<TranslationMap>(url));
      console.log('Translation data loaded:', data);

      this.translations$.next(data); // Mise √† jour des vues

      // Sauvegarde dans le Cache M√©moire
      this.memoryCache.set(cacheKey, data);

    } catch (err) {
      console.error('Error loading translation file:', err);
    }
  }

  async switchLanguage(lang: string) {
    this.currentLang$.next(lang);
    await this.initWording();
  }

  get(key: string, params?: { [key: string]: string | number }): string {
    const keys = key.split('.');
    let result: string | TranslationMap | undefined = this.translations$.value;

    for (const k of keys) {
      if (result && typeof result === 'object' && !Array.isArray(result)) {
        result = (result as TranslationMap)[k];
      } else {
        return key; // Cl√© introuvable ou chemin invalide
      }
    }

    // Si le r√©sultat est une cha√Æne, on applique les param√®tres si pr√©sents
    if (typeof result === 'string') {
      if (params) {
        return result.replace(/\{\{\s*([a-zA-Z0-9_]+)\s*\}\}/g, (match, paramKey) => {
          const value = params[paramKey];
          return value !== undefined ? String(value) : match;
        });
      }
      return result;
    }

    return key;
  }
}
